.content .content_li {
  padding: 10px 0px;
}
.content .content_li .content_li_ques {
  display: -webkit-flex;
  /* Safari */
  display: flex;
  flex-wrap: nowrap;
}
.content .content_li .content_li_ques .content_li_ques_icon {
  width: 14px;
  height: 15px;
  flex-shrink: 0;
}
.content .content_li .content_li_ques .content_li_ques_icon img {
  width: 100%;
  height: 100%;
}
.content .content_li .content_li_ques .content_li_ques_content {
  font-family: MicrosoftYaHeiSemibold;
  font-size: 12px;
  color: #212121;
  letter-spacing: 0;
  line-height: 18px;
  font-weight: 600;
  padding-left: 9px;
}
.content .content_li .content_li_an {
  display: -webkit-flex;
  /* Safari */
  display: flex;
  flex-wrap: nowrap;
  margin-top: 10px;
}
.content .content_li .content_li_an .content_li_an_icon {
  width: 14px;
  height: 15px;
  flex-shrink: 0;
}
.content .content_li .content_li_an .content_li_an_icon img {
  width: 100%;
  height: 100%;
}
.content .content_li .content_li_an .content_li_an_content {
  font-family: MicrosoftYaHei;
  font-size: 12px;
  color: #212121;
  letter-spacing: 0;
  font-weight: 400;
  padding-left: 9px;
}
